body {
  margin: 0;
  font-family: Nunito, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  box-sizing: border-box;
  min-height: 100vh;
  color: black;
}


.outer-container {
  margin: 0 auto;
}

.styled-table {
  border-collapse: collapse;
  margin: 0px;
  font-size: 15px;
  font-family: Calibri, sans-serif;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.styled-table thead tr {
  background-color: palevioletred;   
  color: white;
  text-align: left;
}

.styled-table th,
.styled-table td {
  padding: 3px 5px;
}

.styled-table td {
  color: #333;
}

.styled-table tbody tr {
  border-bottom: 1px solid #ddd;
}

.styled-table tbody tr:nth-of-type(even) {
  background-color: #eee;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 2px solid #ccc;
}

.styled-table tbody tr:hover {
  background-color: rgb(232, 214, 250);
}

.styled-table a {
  color: navy;
}

.styled-table a:hover {
  color: MediumVioletRed;
}





html {
    --navbarBackgroundColor: DarkSlateBlue;
    --tabBackgroundColor: MediumSlateBlue;
    --tabHoverBackgroundColor: lightblue;
    --activeTabLinkTextColor: DarkSlateBlue;
}

.nav-container {
    width: 100%;
    height: 90px;
    background-color: DarkSlateBlue;
    background-color: var(--navbarBackgroundColor);
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
}

.logo {
    font-family: Nunito;
    color: white;
    text-decoration: none;
    font-size: 23px;
    font-weight: 800;
    margin-top: 20px;
}

.nav-ul {
    margin: 0 auto;
    padding: 0px;
    margin-top: 13px;
    list-style: none;
}
    
.nav-ul li.nav-li {
    display: inline-block;
    background: MediumSlateBlue;
    background: var(--tabBackgroundColor);
    padding: 0px;
    margin: 0px 1px;
    border-top-left-radius: 8px; 
    border-top-right-radius: 8px; 
    width: 80px;
}

.nav-ul a {
    display: block;
    color: white;
    text-decoration: none;
    padding: 4px 0px 0 0px;
    margin: 0 auto;
    width: 75px;
    text-align: center;
}

.nav-ul li.nav-li:hover {
    background: lightblue;
    background: var(--tabHoverBackgroundColor);
}

.nav-ul a:hover {
    text-decoration: none;
    color: midnightblue;
}

.nav-ul a.active-class {
    text-decoration: none;
    color: DarkSlateBlue;
    color: var(--activeTabLinkTextColor);
    font-weight: bold; 
}

.nav-ul li.nav-li.active-tab {
    background: white;
    color: DarkSlateBlue;
    color: var(--activeTabLinkTextColor);
}

.nav-ul li.nav-li.active-tab:hover {
    background: lightblue;
    background: var(--tabHoverBackgroundColor);
}

.nav-ul li.nav-li.active-tab a:hover {
    color: navy;
}

.menu-hamburger {
    display: none;
}

.help-button {
    position: absolute;
    top: 10px;
    right: 3%;
    color: white;
    font-size: 24px;
    cursor: pointer;
}


@media screen and (max-width: 900px) {
    .nav-ul a { font-size: 15px; }
    .nav-ul { margin-top: 15px; }
}

@media screen and (max-width: 575px) {
    .logo { font-size: 20px; }
    .nav-ul { margin-top: 20px; }
    .nav-ul a { font-size: 14px; }

    .menu-hamburger {
        display: inline;
        position: absolute;
        left: 20px;
        top: 14px;
        height: 40px;
        width: 30px;
        color: white;
        font-size: 24px;
        cursor: pointer;
    }
}

@media screen and (max-width: 450px) {
    .logo { font-size: 17px; }
    .nav-ul { margin-top: 25px; }
    .menu-hamburger {
        top: 10px;
        font-size: 22px;
    }

    .nav-ul a {
        font-size: 13px;
    }

    .help-button { 
        font-size: 20px;
    }
}

@media screen and (max-width: 415px) {
    .nav-ul { margin-top: 27px; }
    .nav-ul a {
        font-size: 12px;
    }

    .nav-ul li.nav-li { 
        width: 70px;
    }

    .menu-hamburger {
        left: 15px;
        top: 10px;
    }
}

@media screen and (max-width: 385px) {
    .logo { font-size: 16px; } 
    .nav-ul { margin-top: 28px; }

    .menu-hamburger {
        left: 10px;
        font-size: 18px;
    }
}

@media screen and (max-width: 345px) {
    .logo { font-size: 15px; } 
    .nav-ul { margin-top: 30px; }

    .menu-hamburger {
        left: 6px;
        font-size: 16px;
    }
}




